import React from 'react';

const ACTIONS = {
  DEFAULT: 'default',
  ELLIPSE: 'ellipse',
  EMPTY: 'empty',
  LEFT_ELLIPSE: 'left_ellipse',
  RIGHT_ELLIPSE: 'right_ellipse'
};

const paginationReducer = (state, action) => {
  switch (action.type) {
    case ACTIONS.EMPTY: {
      return [];
    }

    case ACTIONS.DEFAULT: {
      const { selectedPage, totalPages } = action.payload;
      return Array.from({ length: totalPages }, (_, i) => ({
        label: i + 1,
        selected: i + 1 === selectedPage
      }));
    }

    case ACTIONS.RIGHT_ELLIPSE: {
      const { pagesToShow, selectedPage, totalPages } = action.payload;
      return Array.from({ length: pagesToShow }, (_, i) => ({
        label: i + 1,
        selected: i + 1 === selectedPage
      })).concat([
        {
          label: '...',
          selected: false
        },
        {
          label: totalPages,
          selected: false
        }
      ]);
    }

    case ACTIONS.LEFT_ELLIPSE: {
      const { pagesToShow, selectedPage, totalPages } = action.payload;
      return [
        {
          label: 1,
          selected: false
        },
        {
          label: '...',
          selected: false
        }
      ].concat(
        Array.from({ length: pagesToShow }, (_, i) => ({
          label: totalPages - (pagesToShow - 1 - i),
          selected: totalPages - (pagesToShow - 1 - i) === selectedPage
        }))
      );
    }

    default: {
      const { pagesToShow, selectedPage, totalPages } = action.payload;
      return [
        {
          label: 1, // page 1
          selected: false
        },
        {
          label: '...',
          selected: false
        }
      ]
        .concat([
          ...Array.from({ length: pagesToShow }, (_, i) => ({
            label: selectedPage - (selectedPage % pagesToShow || pagesToShow) + 1 + i,
            selected: selectedPage - (selectedPage % pagesToShow || pagesToShow) + 1 + i === selectedPage
          }))
        ])
        .concat([
          {
            label: '...',
            selected: false
          },
          {
            label: totalPages,
            selected: selectedPage === totalPages
          }
        ]);
    }
  }
};

const usePaginationReducer = (reducer = paginationReducer, initialState = []) =>
  React.useReducer(reducer, initialState);

const usePagination = ({ pagesToShow, selectedPage, totalPages }) => {
  const [paginationState, dispatch] = usePaginationReducer();

  React.useEffect(() => {
    const payload = {
      pagesToShow,
      selectedPage,
      totalPages
    };
    if (!selectedPage) {
      dispatch({ payload, type: ACTIONS.EMPTY });
    } else if (totalPages <= pagesToShow + 2) {
      // e.g. [1, 2, 3, 4, 5, 6]
      dispatch({ payload, type: ACTIONS.DEFAULT });
    } else if (selectedPage <= pagesToShow) {
      // e.g. [1, 2, 3, 4, 5, 6, ..., 20]
      dispatch({ payload, type: ACTIONS.RIGHT_ELLIPSE });
    } else if (totalPages - selectedPage < pagesToShow) {
      // e.g. [1, ..., x, x, x, x, 20]
      dispatch({ payload, type: ACTIONS.LEFT_ELLIPSE });
      //console.log("WER");
    } else {
      dispatch({ payload });
    }
  }, [dispatch, pagesToShow, selectedPage, totalPages]);

  return paginationState;
};

export { ACTIONS, paginationReducer, usePagination, usePaginationReducer };
