import React from 'react';
import { func, node, number, string } from 'prop-types';
import Pagination from '../pagination';
import PaginationControl from '../pagination-control';
import PaginationOverflow from '../pagination-overflow';
import ButtonIcon from '../button-icon';
import Icon from '../icon';
import PaginationPage from '../pagination-page';
import { usePagination } from '../pagination/usePagination';

const PaginationWrapper = ({
  ariaLabel,
  className,
  firstPageLabel,
  lastPageLabel,
  morePagesLabel,
  nextPageLabel,
  onButtonClick,
  pageAriaLabel,
  pagesToShow,
  previousPageLabel,
  selectedPage,
  totalPages,
  ...remainingProps
}) => {
  const paginationState = usePagination({ pagesToShow, selectedPage, totalPages });

  const handleClick = (e, page) => {
    if (typeof onButtonClick === 'function') {
      onButtonClick(e, page);
    }
  };

  return (
    <Pagination ariaLabel={ariaLabel} className={className} {...remainingProps}>
      <PaginationControl>
        <ButtonIcon
          iconType="light-tiny"
          ariaLabel={firstPageLabel}
          disabled={selectedPage === 1}
          onClick={e => handleClick(e, 1)}
        >
          <Icon name="arrow-start" resolution="tiny" />
        </ButtonIcon>
      </PaginationControl>
      <PaginationControl>
        <ButtonIcon
          iconType="light-tiny"
          ariaLabel={previousPageLabel}
          disabled={selectedPage === 1}
          onClick={e => handleClick(e, selectedPage - 1)}
        >
          <Icon name="arrow-previous" resolution="tiny" />
        </ButtonIcon>
      </PaginationControl>
      {paginationState.map((page, k) => {
        if (page.label === '...') {
          return (
            <PaginationOverflow key={k}>
              <ButtonIcon iconType="light-tiny" ariaLabel={morePagesLabel} disabled>
                <Icon name="options" resolution="tiny" />
              </ButtonIcon>
            </PaginationOverflow>
          );
        }

        return (
          <PaginationPage key={k} selected={page.selected}>
            <ButtonIcon
              iconType="light-tiny"
              ariaLabel={`${pageAriaLabel} ${page.label}`}
              onClick={e => handleClick(e, page.label)}
            >
              {page.label}
            </ButtonIcon>
          </PaginationPage>
        );
      })}
      <PaginationControl>
        <ButtonIcon
          iconType="light-tiny"
          ariaLabel={nextPageLabel}
          disabled={selectedPage === totalPages}
          onClick={e => handleClick(e, selectedPage + 1)}
        >
          <Icon name="arrow-next" resolution="tiny" />
        </ButtonIcon>
      </PaginationControl>
      <PaginationControl>
        <ButtonIcon
          iconType="light-tiny"
          ariaLabel={lastPageLabel}
          disabled={selectedPage === totalPages}
          onClick={e => handleClick(e, totalPages)}
        >
          <Icon name="arrow-end" resolution="tiny" />
        </ButtonIcon>
      </PaginationControl>
    </Pagination>
  );
};

PaginationWrapper.propTypes = {
  /**
   * Aria label
   */
  ariaLabel: string.isRequired,

  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * First page label
   */
  firstPageLabel: string,

  /**
   * Last page label
   */
  lastPageLabel: string,

  /**
   * More pages label
   */
  morePagesLabel: string,

  /**
   * Next page label
   */
  nextPageLabel: string,

  /**
   * Callback fired when a button is clicked.
   */
  onButtonClick: func,

  /**
   * Page aria label
   */
  pageAriaLabel: string,

  /**
   * Pages aria label
   */
  pagesToShow: number,

  /**
   * Previous page label
   */
  previousPageLabel: string,

  /**
   * Selected Page
   */
  selectedPage: number,

  /**
   * Total pages
   */
  totalPages: number.isRequired
};

PaginationWrapper.defaultProps = {
  firstPageLabel: 'Go to first page',
  lastPageLabel: 'Go to last page',
  morePagesLabel: 'more pages',
  nextPageLabel: 'Go to next page',
  pageAriaLabel: 'Go to page',
  pagesToShow: 6,
  previousPageLabel: 'Go to previous page',
  selectedPage: 1
};

PaginationWrapper.displayName = 'PaginationWrapper';

export default PaginationWrapper;
