import React, { useState } from 'react';
import ButtonIcon from '../../button-icon';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Link from '../../link';
import Pagination from '../../pagination';
import PaginationControl from '../../pagination-control';
import PaginationOverflow from '../../pagination-overflow';
import PaginationPage from '../../pagination-page';
import PaginationWrapper from '../../pagination-wrapper';
import Row from '../../row';
import Typography from '../../typography';
import { usePagination } from '../usePagination';

const PaginationExample = () => {
  const [stateOne, setStateOne] = useState({
    pagesToShow: 6,
    selectedPage: 2,
    totalPages: 20
  });

  const [stateTwo, setStateTwo] = useState({
    selectedPage: 2,
    totalPages: 20
  });

  const paginationOneState = usePagination(stateOne);

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Pagination</Typography>
          <Typography>
            Use Buttons for Actions and Links to Navigate
          </Typography>
          <Typography>
            Generally speaking, buttons should only be used for actions
            that are taken within a page and that do not take the user to
            a new page or location. Buttons work better than links for
            single page applications (SPAs).
          </Typography>
          <Typography>
            Links should only be used when the user is being taken to a
            new page. Links work better than buttons for server-side rendered
            apps in which the page reloads.
          </Typography>
          <Typography>
            Pagination Buttons example (view only, no functionality)
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Pagination ariaLabel="Pagination Button example">
            <PaginationControl>
              <ButtonIcon ariaLabel="first page" iconType="light-tiny">
                <Icon name="arrow-start" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
            <PaginationControl>
              <ButtonIcon ariaLabel="previous page" iconType="light-tiny">
                <Icon name="arrow-previous" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
            <PaginationOverflow>
              <ButtonIcon ariaLabel="more pages" iconType="light-tiny" disabled>
                <Icon name="options" resolution="tiny" />
              </ButtonIcon>
            </PaginationOverflow>
            <PaginationPage selected>
              <ButtonIcon ariaLabel="Go to page 1" iconType="light-tiny">1</ButtonIcon>
            </PaginationPage>
            <PaginationOverflow>
              <ButtonIcon ariaLabel="more pages" iconType="light-tiny" disabled>
                <Icon name="options" resolution="tiny" />
              </ButtonIcon>
            </PaginationOverflow>
            <PaginationControl>
              <ButtonIcon ariaLabel="next page" iconType="light-tiny">
                <Icon name="arrow-next" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
            <PaginationControl>
              <ButtonIcon ariaLabel="last page" iconType="light-tiny">
                <Icon name="arrow-end" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
          </Pagination>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Pagination Links example (view only, no functionality)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Pagination ariaLabel="Pagination Link example">
            <PaginationControl>
              <Link aria-label="first page" href="#">
                <Icon name="arrow-start" resolution="tiny" />
              </Link>
            </PaginationControl>
            <PaginationControl>
              <Link aria-label="previous page" href="#">
                <Icon name="arrow-previous" resolution="tiny" />
              </Link>
            </PaginationControl>
            <PaginationOverflow>
              <Link aria-label="more pages" href="#" disabled>
                <Icon name="options" resolution="tiny" />
              </Link>
            </PaginationOverflow>
            <PaginationPage selected>
              <Link aria-label="Go to page 1" href="#">1</Link>
            </PaginationPage>
            <PaginationOverflow>
              <Link aria-label="more pages" href="#" disabled>
                <Icon name="options" resolution="tiny" />
              </Link>
            </PaginationOverflow>
            <PaginationControl>
              <Link aria-label="next page" href="#">
                <Icon name="arrow-next" resolution="tiny" />
              </Link>
            </PaginationControl>
            <PaginationControl>
              <Link aria-label="last page" href="#">
                <Icon name="arrow-end" resolution="tiny" />
              </Link>
            </PaginationControl>
          </Pagination>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Pagination through BYO-State and composition</Typography>
          <Typography>
            Use this approach to create your own Custom Pagination
            control or wrapper based on the shape of your data.
          </Typography>
          <Typography>
            You can reuse the CODE below as the basis for a
            PaginationWrapper in your application.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Pagination ariaLabel="Pagination through bring-your-own state and composition Button example">
            <PaginationControl>
              <ButtonIcon
                ariaLabel="Go to first page"
                disabled={stateOne.selectedPage === 1}
                iconType="light-tiny"
                onClick={() => setStateOne({ ...stateOne, selectedPage: 1 })}
              >
                <Icon name="arrow-start" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
            <PaginationControl>
              <ButtonIcon
                ariaLabel="Go to previous page"
                disabled={stateOne.selectedPage === 1}
                iconType="light-tiny"
                onClick={() => setStateOne({ ...stateOne, selectedPage: stateOne.selectedPage - 1 })}
              >
                <Icon name="arrow-previous" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
            {
              paginationOneState.map((page, key) => (
                page.label === '...'
                  ? <PaginationOverflow key={key}>
                    <ButtonIcon ariaLabel="more pages" iconType="light-tiny" disabled>
                      <Icon name="options" resolution="tiny" />
                    </ButtonIcon>
                  </PaginationOverflow>
                  : <PaginationPage selected={page.selected} key={key}>
                    <ButtonIcon
                      ariaLabel={`${page.selected ? 'Current page, page' : 'Go to page'} ${page.label}`}
                      iconType="light-tiny"
                      onClick={() => setStateOne({ ...stateOne, selectedPage: page.label })}
                    >
                      {page.label}
                    </ButtonIcon>
                  </PaginationPage>
              ))
            }
            <PaginationControl>
              <ButtonIcon
                ariaLabel="Go to next page"
                disabled={stateOne.selectedPage === stateOne.totalPages}
                iconType="light-tiny"
                onClick={() => setStateOne({ ...stateOne, selectedPage: stateOne.selectedPage + 1 })}
              >
                <Icon name="arrow-next" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
            <PaginationControl>
              <ButtonIcon
                ariaLabel="Go to last page"
                disabled={stateOne.selectedPage === stateOne.totalPages}
                iconType="light-tiny"
                onClick={() => setStateOne({ ...stateOne, selectedPage: stateOne.totalPages })}
              >
                <Icon name="arrow-end" resolution="tiny" />
              </ButtonIcon>
            </PaginationControl>
          </Pagination>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Pagination Wrapper (Legacy)</Typography>
          <Typography>
            If this wrapper is not sufficient for
            your case, use the above example to create your
            own wrapper based on the shape of your data.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <PaginationWrapper
            ariaLabel="Pagination Wrapper (Legacy) Button example"
            onButtonClick={(e, page) => setStateTwo({ ...stateTwo, selectedPage: page })}
            selectedPage={stateTwo.selectedPage}
            totalPages={stateTwo.totalPages}
          />
        </Col>
      </Row>
    </Grid>
  );
};

export default PaginationExample;
