import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-pagination-overflow';

const PaginationOverflow = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <li
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </li>
));

PaginationOverflow.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

PaginationOverflow.displayName = 'PaginationOverflow';

export default PaginationOverflow;
