import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-pagination';

const Pagination = forwardRef(({ ariaLabel, children, className, role, ...remainingProps }, ref) => (
  <nav
    aria-label={ariaLabel}
    className={className}
    ref={ref}
    role={role}
    {...remainingProps}
  >
    <ul className={classnames(CSS_PREFIX)}>
      {children}
    </ul>
  </nav>
));

Pagination.propTypes = {
  /**
   * Aria label
   */
  ariaLabel: string.isRequired,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  role: string
};

Pagination.defaultProps = {
  role: 'navigation'
};

Pagination.displayName = 'Pagination';

export default Pagination;
