import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-pagination-page';
const CSS_STATE = 'vds-state';

const PaginationPage = forwardRef(({ children, className, selected, ...remainingProps }, ref) => (
  <li
    aria-current={selected ? 'true' : undefined}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--selected`]: Boolean(selected)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </li>
));

PaginationPage.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   *  Selected
   */
  selected: bool
};

PaginationPage.displayName = 'PaginationPage';

export default PaginationPage;
